<template>
  <div style="background-color: #FFFFF; padding: 30px">
    <div style="padding: 0 70px 70px 70px">
      <div style="font-size: 30px; font-weight: bold; margin: 0 50px 50px 50px">
        大学生心理健康状况分析（数据源：新浪微博 置信概率：>=75%）
      </div>
      <div
        style="height: 300%; width: 33%; display: inline-block"
        v-for="(item, index) in [
          'q0',
          'q1',
          'q2',
          'q3',
          'q4',
          'q5',
          'q6',
          'q7',
          'q8',
        ]"
        :key="index"
        :id="item"
      ></div>
    </div>
    <div style="height: 800px">
      <div id="container" style="height: 100%; width: 100%"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "statistic",
  data() {
    return {
      data: [],
    };
  },
  methods: {
    getBackendData() {
      this.$store.state
        .statisticDataGet(this.$route.query.cid)
        .then((response) => {
          this.data = response.data;
          this.drawLine();
        });
    },
    drawLine() {
      let names = [
        "非必要不出校",
        "封闭",
        "解封",
        "开学",
        "宿舍生活",
        "网课",
        "校园网",
        "疫情下的大学生活",
        "疫情下的异地恋",
      ];
      let mainbar = this.$echarts.init(document.getElementById("container"));
      let totals = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        totals.push(this.data[i].total);
      }
      let optionbar = {
        backgroundColor: "#344b58",
        title: {
          text: "爬取各关键词相关数据条数统计",
          x: "3%",
          y: "3%",
          textStyle: {
            color: "#fff",
            fontSize: "22",
          },
        },

        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
            textStyle: {
              color: "#fff",
            },
          },
        },
        grid: {
          borderWidth: 0,
          top: 110,
          bottom: 95,
          textStyle: {
            color: "#fff",
          },
        },
        calculable: true,
        xAxis: [
          {
            type: "category",
            axisLine: {
              lineStyle: {
                color: "#90979c",
              },
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            splitArea: {
              show: false,
            },
            axisLabel: {
              interval: 4,
            },
            data: names,
          },
        ],
        yAxis: [
          {
            type: "value",
            splitLine: {
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: "#90979c",
              },
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              interval: 0,
            },
            splitArea: {
              show: false,
            },
          },
        ],
        dataZoom: [
          {
            show: true,
            height: 30,
            xAxisIndex: [0],
            bottom: 30,
            start: 0,
            end: 100,
            handleIcon:
              "path://M306.1,413c0,2.2-1.8,4-4,4h-59.8c-2.2,0-4-1.8-4-4V200.8c0-2.2,1.8-4,4-4h59.8c2.2,0,4,1.8,4,4V413z",
            handleSize: "110%",
            handleStyle: {
              color: "#d3dee5",
            },
            textStyle: {
              color: "#fff",
            },
            borderColor: "#90979c",
          },
          {
            type: "inside",
            show: true,
            height: 15,
            start: 1,
            end: 35,
          },
        ],
        series: [
          {
            name: "total",
            type: "bar",
            stack: "总量",
            itemStyle: {
              normal: {
                color: function (params) {
                  // build a color map as your need.
                  var colorList = [
                    "#B5C334",
                    "#FCCE10",
                    "#E87C25",
                    "#27727B",
                    "#FE8463",
                    "#9BCA63",
                    "#FAD860",
                    "#F3A43B",
                    "#60C0DD",
                    "#D7504B",
                    "#C6E579",
                    "#F4E001",
                    "#F0805A",
                    "#26C0C0",
                  ];
                  return colorList[params.dataIndex];
                },

                barBorderRadius: 0,
                label: {
                  show: true,
                  position: "inside",
                  formatter: function (p) {
                    return p.value > 0 ? p.value : "";
                  },
                },
              },
            },
            data: totals,
          },
        ],
      };
      mainbar.setOption(optionbar);


      let main = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        main.push(this.$echarts.init(document.getElementById(`q${i}`)));
      }
      let datas = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        datas.push([0, 0, 0]);
        datas[i][0] = this.data[i].negative;
        datas[i][1] = this.data[i].neutral;
        datas[i][2] = this.data[i].positive;
      }
      let option = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        let option_son = {
          title: {
            text: this.data[i].name,
            left: "center",
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)",
          },
          legend: {
            // orient: 'vertical',
            // top: 'middle',
            bottom: 10,
            left: "center",
            data: ["negative", "neutral", "positive"],
          },
          series: [
            {
              type: "pie",
              radius: "50%",
              center: ["50%", "50%"],
              selectedMode: "single",
              data: [
                { value: datas[i][0], name: "negative" },
                { value: datas[i][1], name: "neutral" },
                { value: datas[i][2], name: "positive" },
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)",
                },
              },
            },
          ],
        };
        option.push(option_son);
      }
      for (let i = 0; i < this.data.length; i = i + 1) {
        main[i].setOption(option[i]);
      }
    },
  },
  beforeMount() {
    this.getBackendData();
  },
};
</script>

<style scoped>
</style>