import Vue from 'vue'
import VueRouter from 'vue-router'
import basePage from "../common/basePage"
import index from "../views/index"
import statistic from "../views/statistic"
import keywordAnalyzing from "../views/keywordAnalyzing"
import paperIntroduction from "../views/paperIntroduction"
import message from "../views/message"
import discussion from "../views/discussion"


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:'/index',
    component: basePage,
    children:[
      {
        path:'index',
        name:"index",
        component:index,
      },
      {
        path:'statistic',
        name:"statistic",
        component:statistic,
      },
      {
        path:'keywordAnalyzing',
        name:"keywordAnalyzing",
        component:keywordAnalyzing,
      },
      {
        path:'paperIntroduction',
        name:"paperIntroduction",
        component:paperIntroduction,
      },
      {
        path:'message',
        name:"message",
        component:message,
      },
      {
        path:'discussion',
        name:"discussion",
        component:discussion,
      },
    ]
  },
  
]

const router = new VueRouter({
  routes
})

export default router
