<template>
  <div>
    <a-layout hasSider>
      <a-layout-sider
        v-model="leftCollapsed"
        collapsible
        id="side"
        :style="{
          overflow: 'auto',
          position: 'fixed',
          left: '0',
          height: '100%',
          zIndex: '10',
        }"
      >
        <div class="logo" style="">
          <span v-show="leftCollapsed == 0">长风杯</span>
        </div>
        <a-menu
          theme="dark"
          mode="inline"
          selectable
          :style="{ height: '100%' }"
          :selectedKeys="selectKeys"
          @click="handelSelect"
        >
          <a-menu-item key="index">
            <a-icon type="home" />
            <span>Index</span>
          </a-menu-item>
          <a-menu-item key="statistic">
            <a-icon type="area-chart" />
            <span>Statistic</span>
          </a-menu-item>
          <a-menu-item key="paperIntroduction">
            <a-icon type="file" />
            <span>Paper Introduction</span>
          </a-menu-item>
          <a-sub-menu key="keywordAnalyzing">
            <span slot="title" style="font-size: 13px">
              <a-icon type="highlight" />
              <span>Keyword Analyzing</span>
            </span>
            <a-menu-item key="非必要不出校">
              <a-icon type="database" />
              <span>非必要不出校</span>
            </a-menu-item>
            <a-menu-item key="封闭">
              <a-icon type="database" />
              <span>封闭</span>
            </a-menu-item>
            <a-menu-item key="解封">
              <a-icon type="database" />
              <span>解封</span>
            </a-menu-item>
            <a-menu-item key="开学">
              <a-icon type="database" />
              <span>开学</span>
            </a-menu-item>
            <a-menu-item key="宿舍生活">
              <a-icon type="database" />
              <span>宿舍生活</span>
            </a-menu-item>
            <a-menu-item key="网课">
              <a-icon type="database" />
              <span>网课</span>
            </a-menu-item>
            <a-menu-item key="校园网">
              <a-icon type="database" />
              <span>校园网</span>
            </a-menu-item>
            <a-menu-item key="疫情下的大学生活">
              <a-icon type="database" />
              <span>疫情下的大学生活</span>
            </a-menu-item>
            <a-menu-item key="疫情下的异地恋">
              <a-icon type="database" />
              <span>疫情下的异地恋</span>
            </a-menu-item>
          </a-sub-menu>
          <a-menu-item key="message">
            <a-icon type="message" />
            <span>Message</span>
          </a-menu-item>
          <a-menu-item key="discussion">
            <a-icon type="usergroup-add" />
            <span>Discussion</span>
          </a-menu-item>
        </a-menu>
      </a-layout-sider>
      <a-layout :style="rightDivStyle" class="rightDiv">
        <a-layout-header
          :style="{
            backgroundColor: '#001529',
            height: '68px',
            width: '100%',
            position: 'fixed',
            zIndex: '1000',
          }"
        >
          <div style="color: white; font-size: 20px; margin-left: -200px">
            大学生心理状态可视化
          </div>
        </a-layout-header>
        <a-layout-content :style="{ paddingTop: '68px' }">
          <router-view></router-view>
          <div style="right:30px;bottom:40px;position:fixed;"><img src="../assets/luoxiaohan.jpg" style="width:100px;height:80px;"></div>
        </a-layout-content>
      </a-layout>
    </a-layout>
  </div>
</template>

<script>
export default {
  name: "basePage",
  components: {},
  data() {
    return {
      leftCollapsed: false,
      selectKeys: [],
      rightDivStyle: {
        paddingLeft: "200px",
      },
      newTag: [],
      keywordAnalyzingArray: [
        "非必要不出校",
        "封闭",
        "解封",
        "开学",
        "宿舍生活",
        "网课",
        "校园网",
        "疫情下的大学生活",
        "疫情下的异地恋",
      ],
    };
  },
  computed: {
    url() {
      return this.$route.name;
    },
  },
  watch: {
    url() {
      this.selectKeys = [this.$route.name];
    },
    leftCollapsed() {
      this.leftCollapsed
        ? (this.rightDivStyle.paddingLeft = "80px")
        : (this.rightDivStyle.paddingLeft = "200px");
    },
  },
  beforeMount() {
    this.selectKeys = [this.$route.name];
  },
  methods: {
    handelSelect(item) {
      //console.log(item)
      if (this.keywordAnalyzingArray.includes(item.key)) {
        this.$router.push("/keywordAnalyzing/?name=" + item.key);
        this.$router.go(0);
      } else {
        if (item.key != this.selectKeys[0]) {
          this.$router.push("/" + item.key);
        }
      }
    },
  },
};
</script>

<style scoped>
.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
  color: white;
  font-size: 22px;
}
aside#side::-webkit-scrollbar {
  display: none;
}
section.rightDiv.ant-layout {
  transition: padding-left 0.2s;
  -moz-transition: padding-left 0.2s; /* Firefox 4 */
  -webkit-transition: padding-left 0.2s; /* Safari and Chrome */
  -o-transition: padding-left 0.2s; /* Opera */
}
</style>