<template>
  <div style="background-color:#FFFFF;padding:30px;">
    <a-card :title=name>
      <a-table :columns="columns" :data-source="data" :rowKey="'id'"></a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: "内容",
    dataIndex: "content",
    
  },
  {
    title: "分类",
    dataIndex: "classification",
    filters: [
      {
        text: "positive",
        value: "positive",
      },
      {
        text: "negative",
        value: "negative",
      },
      {
        text: "neutral",
        value: "neutral",
      },
    ],
    onFilter: (value, record) => record.classification.includes(value),
  },
  {
    title: "可信度",
    dataIndex: "confidenceCoefficient",
    sorter: (a, b) => a.confidenceCoefficient > b.confidenceCoefficient,
  },
  {
    title: "积极概率",
    dataIndex: "positive",
    sorter: (a, b) => a.positive > b.positive,
  },
  {
    title: "消极概率",
    dataIndex: "negative",
    sorter: (a, b) => a.negative > b.negative,
  },
]
export default {
  name: "keywordAnalyzing",
  data() {
    return {
      data: [],
      name:this.$route.query.name,
      columns,
    };
  },
  methods: {
    getBackendData() {
      this.$store.state.keywordAnalyzingDataGet(this.name).then((response) => {
        this.data = response.data;
      });
    },
  },
  beforeMount() {
    
    this.getBackendData()
    
  }
};
</script>

